export const listOffers =`
  query ListOffers($limit: Int, $nextToken: String) {
    listOffers(limit: $limit, nextToken: $nextToken) {
      items {
        Funding {
          PurchaseType
          LoanType
          DownPayment
        }
        OfferNo
        OfferId
        PropertyData {
          FullAddress
          OwnerNames
        }
        CounterOffer {
          SellerCounter {
            OfferPrice
          }
          BuyerCounter {
            OfferPrice
          }
        }
        Step {
          messages {
            advisor
            buyer
            seller
          },
          id
          name
        }
        CreatedAt
        UpdatedAt
      }
      nextToken
    }
  }
`;
export const listProperties =`
  query ListProperties($limit: Int, $nextToken: String) {
    listProperties(limit: $limit, nextToken: $nextToken) {
      items {
        CreatedAt
        DTPropertyId
        PropertyData {
          City
          County
          FullAddress
          OwnerNames
          State
          StreetAddress
          Zip
        }
        PropertyId
        PropertyNo
      }
      nextToken
    }
  }
`;
export const listForms =`
  query ListForms($limit: Int, $nextToken: String) {
    listForms(limit: $limit, nextToken: $nextToken) {
      items {
        CategoryId
        CreatedAt
        FormId
        FormStatus
        FormTypeId
        Template
        Title
        UpdatedAt
      }
      nextToken
    }
  }
`;
